// theme.js

import { useState, useEffect } from "react";

const useTheme = () => {
  const [theme, setTheme] = useState("tenma");

  useEffect(() => {
    const currentTheme = localStorage.getItem("theme");
    if (currentTheme) {
      setTheme(currentTheme);
    } else {
      const prefersDarkMode = window.matchMedia(
        "(prefers-color-scheme: dark)"
      ).matches;
      if (prefersDarkMode) {
        setTheme("johan");
      }
    }
  }, []);

  const toggleTheme = () => {
    const newTheme = theme === "johan" ? "tenma" : "johan";
    setTheme(newTheme);
    localStorage.setItem("theme", newTheme);
    document.documentElement.classList.toggle("tenma");
    document.documentElement.classList.toggle("johan");
  };

  return [theme, toggleTheme];
};

export default useTheme;
